  overflow-x: hidden;
}

/* page hidden for SPA */
.page {
  display: none;
  animation: fadeIn 0.3s;
  width: 100%;
}

.hide {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s 0.4s, opacity 0.4s linear;
}

.wrapper {
  display: grid;
  height: 100vh;
  grid-template-rows: auto 64px;
  grid-template-columns: 1fr;
  grid-template-areas: "main" "navbar";
}

main {
  grid-area: main;
  display: grid;
  grid-template-rows: 116px auto;
  grid-template-columns: 1fr;
  grid-template-areas: "head" "contents";
}

.page__head {
  grid-area: head;
  padding-top: 56px;
  padding-bottom: 16px;
  background-color: white;
  width: 100%;
  display: flex;
  position: fixed;
  justify-content: flex-end;
  top: 0;
  right: 0;
  z-index: 3;
}

.heading {
  height: auto;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.heading h1 {
  font-family: "IBM Plex Mono", monospace;
  font-size: 42px;
  line-height: 48px;
  font-weight: 400;
  align-content: flex-end;
  padding-left: 24px;
  border-bottom: 2px solid inherit;
}
.heading .btn-home {
  height: auto;
}

.page__contents {
  grid-area: contents;
  border-top-left-radius: 36px;
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  margin-top: 172px;
  background-color: #fafafa;
  box-shadow: 0px -32px 0px #5667d7;
  z-index: 4;
}
.page__contents--todo {
  padding-top: 32px;
}
.page__contents .page__tabs {
  display: flex;
  width: 100%;
  height: auto;
}
.page__contents .btn-home-add {
  position: absolute;
  right: 24px;
  top: -52px;
}
.page__contents-appointment {
  border-color: #e69537;
  box-shadow: 0px -32px 0px #e69537;
}
.page__contents-meetup {
  border-color: #4a1890;
  box-shadow: 0px -32px 0px #4a1890;
}
.page__contents-Low {
  border-color: #30a644;
  box-shadow: 0px -32px 0px #30a644;
}
.page__contents-Medium {
  border-color: #f2c02c;
  box-shadow: 0px -32px 0px #f2c02c;
}
.page__contents-High {
  border-color: #f25a2c;
  box-shadow: 0px -32px 0px #f25a2c;
}
.page__contents-happy {
  border-color: #30a644;
  box-shadow: 0px -32px 0px #30a644;
}
.page__contents-ok {
  border-color: #f2c02c;
  box-shadow: 0px -32px 0px #f2c02c;
}
.page__contents-sad {
  border-color: #f25a2c;
  box-shadow: 0px -32px 0px #f25a2c;
}

.tabs {
  display: flex;
  gap: 40px;
  width: 100%;
  padding: 0px 24px;
}
.tabs__link {
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  border: none;
  border-bottom: 2px solid transparent;
  padding: 0px 8px;
  background: none;
  text-transform: capitalize;
  color: #6e6e6e;
  line-height: 24px;
  transition: all 0.3s ease-in-out;
}
.tabs__link:active, .tabs__link:hover {
  content: "";
  position: relative;
  color: black;
  border-bottom: 2px solid black;
}

