/* BUTTONS */
.btn, .btn-secondary, .btn-primary, .btn-primary-add {
  font-family: "IBM Plex Sans", sans-serif;
  font-style: normal;
  font-size: 12px;
  line-height: 100%;
  font-weight: 500;
  text-rendering: optimizeLegibility;
  border: 2px solid;
  border-color: black;
  border-radius: 8px;
  cursor: pointer;
  padding: 8px 16px;
  transition: all 0.5s ease;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}
.btn a, .btn-secondary a, .btn-primary a, .btn-primary-add a {
  text-decoration: none;
  line-height: 20px;
}

.btn-primary, .btn-primary-add {
  border: 2px solid black;
  background-color: black;
  color: white;
}
.btn-primary:hover, .btn-primary-add:hover {
  background-color: white;
  color: black;
}

.btn-primary-add {
  padding-left: 8px;
}
.btn-primary-add:before {
  font-family: "tc-icons" !important;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  font-size: 24px;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  content: "\e907";
  font-size: 20px;
  padding-right: 8px;
  color: white;
  transition: color 0.5s ease;
}
.btn-primary-add:hover:before {
  color: black;
}

.btn-secondary {
  background-color: white;
  border-color: black;
  color: black;
}
.btn-secondary:hover {
  background-color: black;
  color: white;
}

.btn-home {
  height: 100%;
  background: none;
  padding-left: 8px;
  padding-right: 32px;
  padding: 8px 32px 7px 8px;
  border: 1px solid black;
  border-right: none;
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
  text-decoration: none;
}
.btn-home i {
  color: black;
  font-size: 20px;
  line-height: 20px;
}

.btn-edit {
  border: 2px solid;
  border-color: black;
  border-radius: 8px;
}

.btn-edit:hover {
  background: #0e29d8;
  color: #fff;
}

.btn-delete {
  color: #f25a2c;
  border: 1px solid #f25a2c;
}
.btn-delete :before {
  content: "";
  color: inherit;
}

.btn-delete:hover {
  background: #f25a2c;
  color: white;
}

.cards {
  width: 100%;
  height: 100%;
  max-width: 480px;
  min-height: 600px;
  padding: 0px 24px 32px;
  display: flex;
  flex-direction: column;
  gap: 24px;
  overflow-y: scroll;
}

.card {
