  background-color: white;
  width: 100%;
  padding: 16px;
  padding-bottom: 8px;
  height: 100%;
  max-height: 96px;
  gap: 16px;
  transition: all 0.3s ease-in-out;
}
.card:hover {
  cursor: pointer;
}
.card-container {
