  border-radius: 4px;
}
.card-Low {
  border-color: #30a644;
  box-shadow: 6px 6px 0px #30a644;
}
.card-Low-line {
  background-color: #30a644;
  width: 8px;
  border-radius: 4px;
}
.card-Medium {
  border-color: #f2c02c;
  box-shadow: 6px 6px 0px #f2c02c;
}
.card-Medium-line {
  background-color: #f2c02c;
  width: 8px;
  border-radius: 4px;
}
.card-High {
  border-color: #f25a2c;
  box-shadow: 6px 6px 0px #f25a2c;
}
.card-High-line {
  background-color: #f25a2c;
  width: 8px;
  border-radius: 4px;
}
.card-happy {
  border-color: #30a644;
  box-shadow: 6px 6px 0px #30a644;
}
.card-happy-line {
  background-color: #30a644;
  width: 8px;
  border-radius: 4px;
}
.card-ok {
  border-color: #f2c02c;
  box-shadow: 6px 6px 0px #f2c02c;
}
.card-ok-line {
  background-color: #f2c02c;
  width: 8px;
