.form {
  display: flex;
  flex-direction: column;
  color: #6d6d6d;
}

input[type=text],
input[type=date],
textarea,
select {
  height: 48px;
  width: 100%;
  padding: 10px;
  border: 2px solid;
  border-color: #b3b3b3;
  border-radius: 8px;
}

label {
  display: block;
  padding-bottom: 6px;
}

fieldset {
  border: none;
  margin-bottom: 16px;
}
fieldset.radio-btns {
  margin-bottom: 24px;
}
fieldset.radio-btns input[type=radio] {
  opacity: 0;
  position: fixed;
  width: 0;
}
fieldset.radio-btns label {
  display: inline-block;
  background-color: #ddd;
  padding: 10px 20px;
  font-family: sans-serif, Arial;
  font-size: 16px;
  border: 2px solid #444;
  border-radius: 4px;
}
fieldset.radio-btns input[type=radio]:checked + label {
  background-color: #bfb;
  border-color: #4c4;
}
fieldset.radio-btns input[type=radio]:focus + label {
  border: 2px dashed #444;
}
fieldset.radio-btns label:hover {
  background-color: #dfd;
}

.form-user-profile {
  max-width: 320px;
}
.form-user-profile label {
  font-family: "IBM Plex Mono", monospace;
  font-size: 12px;
  line-height: 16px;
  font-weight: 300;
  text-rendering: optimizeLegibility;
}
.form-user-profile input {
  border: 2px solid;
  border-color: black;
  border-radius: 8px;
  height: 48px;
  width: 100%;
}

header {
  position: fixed;
  width: 100%;
  bottom: 0;
  grid-area: navbar;
  z-index: 5;
  height: 64px;
}

.navbar {
  bottom: 0;
  left: 0;
  display: flex;
  background-color: white;
  justify-content: center;
  align-items: top;
  border-top: 1px solid #e6e6e6;
}
.navbar-links {
  display: flex;
  width: 100%;
  justify-content: space-evenly;
}
.navbar-links a {
  position: relative;
  width: 32px;
  padding-top: 16px;
  color: #6e6e6e;
  text-decoration: none;
  text-align: center;
  display: flex;
  justify-content: center;
}
.navbar-links a:hover, .navbar-links a:active {
  color: black;
}
.navbar-links a:hover:before, .navbar-links a:active:before {
  content: "";
  position: absolute;
  top: 0;
  border-top: 2px solid black;
  width: 100%;
}
.navbar-links a i {
  width: 24px;
  height: auto;
}
.navbar-links__text {
  display: none;
}

/* Modal */
.modal-wrapper {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  bottom: 0;
  display: none;
  transition: height 0.5s ease-in-out, opacity, 0.5s;
  z-index: 50;
}
.modal-wrapper .modal {
  position: relative;
  background: #fff;
  width: 100%;
  height: 100%;
  margin: 0;
}
.modal-wrapper .modal-header {
  display: flex;
  align-items: center;
  width: 100%;
  height: 128px;
  padding: 2rem;
  background: #f8fafb;
}
.modal-wrapper .modal-body {
  padding: 2rem;
}

.btn-modal {
  background: var(--primary-color);
  border: none;
  color: white;
  width: 9rem;
}